import React, { Component } from 'react';
import {
    Card, CardImg, CardText, CardBody, CardTitle, Breadcrumb, BreadcrumbItem,
    Button, Row, Col, Label, Modal, ModalHeader, ModalBody
} from 'reactstrap';
import { Loading } from './LoadingComponent';
import { Control, LocalForm, Errors } from 'react-redux-form';
import { Link } from 'react-router-dom';
import { baseUrl } from '../shared/baseUrl';
import { FadeTransform, Fade, Stagger } from 'react-animation-components';


function RenderDish({ dish }) {
    if (dish != null) {
        return (
            <FadeTransform
                in
                transformProps={{
                    exitTransform: 'scale(0.5) translateY(-50%)'
                }}>
                <Card>
                    <CardImg width="100%" src={baseUrl + dish.image} alt={dish.name} />
                    <CardBody>
                        <CardTitle>{dish.name}</CardTitle>
                        <CardText>{dish.description}</CardText>
                    </CardBody>
                </Card>
            </FadeTransform>)
    }
}

function RenderComments({ comments, postComment, dishId }) {
    if (comments === undefined) { return }
    else {
        const commentsText = comments.map((comment) => {
            return (
                <Fade in>
                    <li key={comment.id}>
                        <p>{comment.comment}</p>
                        <p>--{comment.author},
                            &nbsp;
                            {new Intl.DateTimeFormat('en-US', {
                                year: 'numeric',
                                month: 'long',
                                day: "2-digit"
                            }).format(new Date(comment.date))}
                        </p>
                    </li>
                </Fade>
            );
        });

        return (
            <div className="container">
                <h4>Comments</h4>
                <ul className="list-unstyled">
                    <Stagger in>{commentsText}</Stagger>
                    <CommentForm dishId={dishId} postComment={postComment} />
                </ul>
            </div>)
    }

}

const DishDetail = (props) => {
    if (props.isLoading) {
        return (
            <div className="container">
                <div className="row">
                    <Loading />
                </div>
            </div>
        );
    }
    else if (props.errMess) {
        return (
            <div className="container">
                <div className="row">
                    <h4>{props.errMess}</h4>
                </div>
            </div>
        );
    }
    else if (props.dish != null) {
        return (
            <div className="container">
                <div className="row">
                    <Breadcrumb>

                        <BreadcrumbItem><Link to="/menu">Menu</Link></BreadcrumbItem>
                        <BreadcrumbItem active>{props.dish.name}</BreadcrumbItem>
                    </Breadcrumb>
                    <div className="col-12">
                        <h3>{props.dish.name}</h3>
                        <hr />
                    </div>
                </div>
                <div className='row'>
                    <div className="col-12 col-md-5 m-1">
                        <RenderDish dish={props.dish} />
                    </div>
                    <div className="col-12 col-md-5 m-1">
                        <RenderComments comments={props.comments}
                            postComment={props.postComment}
                            dishId={props.dish.id}
                        />
                    </div>
                </div>
            </div>
        );
    }
}

export default DishDetail;


const required = (val) => val && val.length;
const maxLength = (len) => (val) => !(val) || (val.length <= len);
const minLength = (len) => (val) => val && (val.length >= len);

export class CommentForm extends Component {

    constructor(props) {
        super(props)

        this.state = {
            isModalOpen: false,
        };

        this.toggleModal = this.toggleModal.bind(this);
        this.handleSubmit = this.handleSubmit.bind(this);
    }

    toggleModal() {
        this.setState({ isModalOpen: !this.state.isModalOpen });
    }

    handleSubmit(values) {
        this.toggleModal();
        console.log('comment:', values);
        alert('comment:' + JSON.stringify(values));
        this.props.postComment(this.props.dishId, values.rating, values.author, values.comment);
    }


    render() {

        return (
            <div className="container">
                <div className="row row-content">
                    <Button outline onClick={this.toggleModal}>
                        <span className="fa fa-pencil fa-lg"> Submit comment</span>
                    </Button>
                    <div className="row row-content">
                        <Modal isOpen={this.state.isModalOpen} toggle={this.toggleModal}>
                            <ModalHeader toggle={this.toggleModal}> Submit comment</ModalHeader>
                            <ModalBody>
                                <div className="col-12 col-md-9">
                                    <LocalForm onSubmit={(values) => this.handleSubmit(values)} >
                                        <Row className="form-group">
                                            <Label htmlFor="rating">Rating</Label>
                                            <Col md={10}>
                                                <Control.select model=".rating" name="rating" className="form-control" >
                                                    <option>1</option>
                                                    <option>2</option>
                                                    <option>3</option>
                                                    <option>4</option>
                                                    <option>5</option>
                                                </Control.select>
                                            </Col>
                                        </Row>

                                        <Row className="form-group">
                                            <Label htmlFor="author" md={3}>Author</Label>
                                            <Col md={50}>
                                                <Control.text model=".author" id="author" name="author" placeholder="Author" className="form-control" validators={{ required, minLength: minLength(2), maxLength: maxLength(15) }} />
                                                <Errors className="text-danger" model=".author" show="touched" messages={{ required: 'Required', minLength: 'Must be greater than 2 characters', maxLength: 'Must be 15 charaters or less' }} />
                                            </Col>
                                        </Row>

                                        <Row className="form-group">
                                            <Label htmlFor="feedback" md={3}>Comment</Label>
                                            <Col md={50}>
                                                <Control.textarea model=".message" id="message" name="message" rows="6" className="form-control" validators={{ required }} />
                                                <Errors className="text-danger" model=".message" show="touched" messages={{ required: 'Required' }} />
                                            </Col>
                                        </Row>

                                        <Button type="submit" value="submit" color="primary">Submit</Button>
                                    </LocalForm>
                                </div>
                            </ModalBody>
                        </Modal>
                    </div>
                </div>
            </div>
        );
    }
}